@import '@/assets/sass/variables';

.ol-control-edit-mode {
  padding: 0 !important;

  &.is-primary {
    box-shadow: none;
  }

  .ol-control-edit-mode-inner {
    padding: 0.3rem 0.25rem 0.2rem;
    border-radius: 4px;
    background: $white;
    white-space: nowrap;
  }

  &.is-primary .ol-control-edit-mode-inner {
    background: $grey-dark;
    color: $white;
  }
}
